import React, { useState, useEffect } from 'react'
import { Table, Tooltip, Button, Collapse, Switch, Popconfirm } from 'antd'
import { PlusOutlined } from '@ant-design/icons'

// 
import FormModal from './AlertForm'

export default function BasisTable(props) {
  const { loading } = props

  const [visible, setVisible] = useState(false)
  const [title, setTitle] = useState('')
  const [rows, setRows] = useState(null)
  const [tableIndex, setTableIndex] = useState(-1)
  const [pagination, setPagination] = useState({
    current: 1,
    pageSize: 10,
    showSizeChanger: true, //
    pageSizeOptions: [10, 20, 50, 100, 1000]
  })
  // 
  const [tableData, setTableData] = useState([
    {
      key: 1,
      name: '',
      age: 30,
      radio: 1,
      address: '1'
    },
    {
      key: 2,
      name: '',
      age: 42,
      radio: 2,
      address: '2'
    }
  ])
  // 
  const columns = [
    {
      title: '',
      dataIndex: 'index',
      key: 'index1',
      width: 60,
      align: 'center',
      render: (text, record, index) => <span>{record.key}</span>
    },
    {
      title: '',
      dataIndex: 'name',
      key: 'name',
      width: 200,

      ellipsis: {
        //
        showTitle: false //
      },
      render: name => (
        <Tooltip placement='topLeft' title={name}>
          {name}
        </Tooltip>
      )
    },
    {
      title: '',
      dataIndex: 'age',
      key: 'age',
      width: 100,
      // defaultSortOrder: 'descend', //
      sorter: (a, b) => a.age - b.age
    },
    {
      title: '',
      dataIndex: 'radio',
      key: 'radio',
      width: 200,

      render: (_, record, index) => (
        <Popconfirm
          title={`${record.radio === 1 ? '' : ''}-----`}
          placement='bottomRight'
          onConfirm={() => confirm(record, index)}
          okText=''
          cancelText=''
        >
          {/* <Switch checked={record.radio == 1} onClick={() => hadleSwitch(record, index)} /> */}
          <Switch checked={record.radio === 1} />
        </Popconfirm>
      )
    },
    {
      title: '',
      dataIndex: 'address',
      key: 'address',
      width: 100,
      ellipsis: true //
    },
    {
      title: '',
      dataIndex: 'address',
      key: 'address',
      width: 1000,
      filters: [
        {
          text: '',
          value: ''
        },
        {
          text: '',
          value: ''
        }
      ],
      onFilter: (value, record) => record.address.indexOf(value) === 0
    },
    {
      title: '',
      dataIndex: '',
      key: 'x',
      fixed: 'right',
      width: 200,
      render: (_, record, index) => (
        <>
          <Button type='link' onClick={() => handleModal('', index, record)}>
            
          </Button>
          <Button type='link' onClick={() => handleModal('', index, record)}>
            
          </Button>
          <Popconfirm title={''} placement='top' onConfirm={() => handleDelete(record.key)} okText='' cancelText=''>
            <Button type='link'></Button>
          </Popconfirm>
        </>
      )
    }
  ]
  let arr = []
  for (let i = 0; i < 20; i++) {
    arr.push({
      key: i + 3,
      name: `Edward King ${i + 3}`,
      age: 32,
      radio: 2,
      address: `${i + 3}`
    })
  }

  useEffect(() => {
    setTableData([...tableData, ...arr])

    setPagination({
      ...pagination,
      total: [...tableData, ...arr].length
    })
    console.log(pagination, '==')
  }, [])

  //
  const onChange = (pagination, filters, sorter, extra) => {
    console.log(pagination, filters, sorter, extra)
    setPagination({
      ...pagination
    })
  }
  // 
  const confirm = (rows, index) => {
    console.log(rows, index)
    let arr = JSON.parse(JSON.stringify(tableData))
    arr[index].radio = arr[index].radio === 1 ? 2 : 1
    setTableData(arr)
  }

  // 
  const handleModal = (type, index, row = null) => {
    setTitle(type)
    setVisible(true)
    setRows(row)
    setTableIndex(index)
  }
  // 
  const handleDelete = key => {
    console.log(key)
    const newData = tableData.filter(item => item.key !== key)
    setTableData(newData)
    setPagination({
      ...pagination,
      total: newData.length
    })
  }

  // --
  const handleOk = data => {
    console.log('--', data)
    setVisible(false)
    if (title === '') {
      data.key = tableData.length + 1
      setTableData([...tableData, data])
    } else {
      data.key = tableData[tableIndex].key
      let arr = JSON.parse(JSON.stringify(tableData))
      arr[tableIndex] = data
      setTableData([...arr])
      console.log(tableData)
    }
  }
  return (
    <Collapse defaultActiveKey={['1']} ghost expandIconPosition='end'>
      <Collapse.Panel header='' key='1'>
        <Button type='primary' icon={<PlusOutlined />} onClick={() => handleModal('', -1)}>
          
        </Button>
        <Table
          dataSource={tableData}
          columns={columns}
          bordered={false}
          loading={loading}
          pagination={pagination}
          scroll={{ y: 240 }}
          title={() => ''}
          onChange={onChange}
          //
          onHeaderRow={(columns, index) => ({
            onClick: event => {
              console.log('click')
            }, // 
            onDoubleClick: event => {
              console.log('dbclick')
            },
            onContextMenu: event => {
              console.log('onContextMenu')
            },
            onMouseEnter: event => {
              console.log('onMouseEnter')
            }, // 
            onMouseLeave: event => {
              console.log('onMouseLeave')
            } // 
          })}
          //
          onRow={record => ({
            onClick: event => {
              console.log('click')
            }, // 
            onDoubleClick: event => {
              console.log('dbclick')
            },
            onContextMenu: event => {
              console.log('onContextMenu')
            },
            onMouseEnter: event => {
              console.log('onMouseEnter')
            }, // 
            onMouseLeave: event => {
              console.log('onMouseLeave')
            } // 
          })}
        />
        {/*  */}
        <FormModal title={title} isModalVisible={visible} rows={rows} handleOk={handleOk} handleCancel={() => setVisible(false)} />
      </Collapse.Panel>
    </Collapse>
  )
}
