import React, { useEffect } from 'react'
import { Form, Modal, Button, Input, Radio } from 'antd'

// 
const initialValues = {
  radio: 1
}

export default function AlertForm({ title, isModalVisible, rows, handleOk, handleCancel }) {
  const [form] = Form.useForm()

  //
  const onOk = val => {
    form
      .validateFields()
      .then(values => {
        form.resetFields()
        handleOk(values)
      })
      .catch(info => {
        console.log('Validate Failed:', info)
      })
  }
  const onCancel = () => {
    form.resetFields()
    handleCancel()
  }
  const footers =
    title === ''
      ? null
      : [
          <Button key='cancel' onClick={handleCancel}>
            
          </Button>,
          <Button key='submit' type='primary' onClick={onOk}>
            
          </Button>
        ]

  //
  useEffect(() => {
    if (isModalVisible) {
      console.log(rows, isModalVisible, 'rows----------')
      form.setFieldsValue(rows)
    }
  }, [isModalVisible])

  return (
    // onCancel:   X  onOk
    <Modal forceRender width={500} title={title} visible={isModalVisible} footer={footers} onOk={onOk} onCancel={onCancel}>
      <Form form={form} layout='vertical' initialValues={initialValues} disabled={title === ''}>
        <Form.Item name='name' label='' rules={[{ required: true, message: '!' }]}>
          <Input placeholder='' />
        </Form.Item>
        <Form.Item name='age' label=''>
          <Input placeholder='' />
        </Form.Item>
        <Form.Item name='address' label=''>
          <Input type='textarea' placeholder='' />
        </Form.Item>
        <Form.Item name='radio'>
          <Radio.Group>
            <Radio value={1}></Radio>
            <Radio value={2}></Radio>
          </Radio.Group>
        </Form.Item>
      </Form>
    </Modal>
  )
}
