import React, { useState, useEffect } from 'react'
import { Table, Collapse, Divider, Radio, Button, Space } from 'antd'

// 

export default function SelectTable({ loading }) {
  const [selectionType, setSelectionType] = useState('checkbox')
  const [selectedRowKeys, setSelectedRowKeys] = useState([1])
  const [pagination, setPagination] = useState({
    current: 1,
    pageSize: 10,
    showSizeChanger: true, //
    pageSizeOptions: [10, 20, 50, 100, 1000]
  })
  // 
  const [tableData, setTableData] = useState([
    {
      key: 1,
      name: '',
      age: 30,
      radio: 1,
      address: '1'
    },
    {
      key: 2,
      name: '',
      age: 42,
      radio: 2,
      address: '2'
    }
  ])
  // 
  const columns = [
    {
      title: '',
      dataIndex: 'index',
      key: 'index1',
      width: 60,
      align: 'center',
      render: (text, record, index) => <span>{index + 1}</span>
    },
    {
      title: '',
      dataIndex: 'name',
      key: 'name'
    },
    {
      title: '',
      dataIndex: 'age',
      key: 'age',
      // defaultSortOrder: 'descend', //
      sorter: (a, b) => a.age - b.age
    },
    {
      title: '',
      dataIndex: 'address',
      key: 'address'
    }
  ]
  let arr = []
  for (let i = 0; i < 50; i++) {
    arr.push({
      key: i + 3,
      name: `Edward King ${i + 3}`,
      age: 32,
      radio: 2,
      address: `${i + 3}`
    })
  }

  useEffect(() => {
    setTableData([...tableData, ...arr])

    setPagination({
      ...pagination,
      total: [...tableData, ...arr].length
    })
  }, [])

  //
  const onChange = (pagination, filters, sorter, extra) => {
    console.log(pagination, filters, sorter, extra)
    setPagination({
      ...pagination
    })
  }
  const selectTableAll = () => {
    const arr = tableData.map(i => (i.name !== '' ? i.key : false))
    setSelectedRowKeys(arr)
  }

  const rowSelection = {
    type: selectionType, //  
    selectedRowKeys, // 
    selections: [Table.SELECTION_ALL, Table.SELECTION_INVERT, Table.SELECTION_NONE],
    onChange: (selectedRowKeys, selectedRows) => {
      console.log(`key: ${selectedRowKeys}`, ': ', selectedRows)
      console.log('selectedRowKeys changed: ', selectedRowKeys)
      setSelectedRowKeys(selectedRowKeys)
    },
    getCheckboxProps: record => ({
      disabled: record.name === '',
      // Column configuration not to be checked
      name: record.name
    })
  }

  return (
    <Collapse defaultActiveKey={['0']} ghost expandIconPosition='end'>
      <Collapse.Panel header='/' key='1'>
        <Radio.Group
          onChange={({ target: { value } }) => {
            setSelectionType(value)
          }}
          value={selectionType}
        >
          <Radio value='checkbox'></Radio>
          <Radio value='radio'></Radio>
        </Radio.Group>

        <Space size='small'>
          <Button type='primary' onClick={() => selectTableAll()}>
            
          </Button>
          <Button onClick={() => setSelectedRowKeys([])}></Button>
        </Space>

        <Divider />
        <Table
          rowSelection={rowSelection}
          dataSource={tableData}
          columns={columns}
          bordered
          loading={loading}
          pagination={pagination}
          scroll={{ y: 240 }}
          title={() => ''}
          onChange={onChange}
        />
      </Collapse.Panel>
    </Collapse>
  )
}
